    .classify {
        // height: 850px;
        padding-top: 100px;
        padding-bottom: 100px;
        h2 {
            width: 450px;
            font-size: 30px;
            margin: 0 auto;
            font-weight: normal;
            text-align: center;
            .tit {
                span {
                    color: #ec593f;
                    font-size: 46px;
                }
            }
            .tit-en {
                font-size: 14px;
                margin-top: 5px;
            }
        }
        .class-list1,
        .class-list2,
        .class-list3 {
            width: 1100px;
            // padding-top: 50px;
            margin: 100px auto 0;
            padding-bottom: 30px;
            li {
                position: relative;
                float: left;
                width: 60px;
                height: 80px;
                margin-right: 190px;
                cursor: pointer;
                img {
                    position: absolute;
                    top: 0;
                    display: block;
                    width: 60px;
                    height: 60px;
                    background-color: #ccc;
                }
                .icon-hover {
                    display: none;
                }
                &:hover .icon-hover {
                    display: block!important;
                }
                &:hover .icon {
                    display: none;
                }
                &:hover p {
                    color: #ec593f;
                }
                .icon-active {
                    display: block;
                }
                .text-active {
                    color: #ec593f;
                }
                p {
                    position: absolute;
                    bottom: 0;
                    width: 100%;
                    font-size: 14px;
                    text-align: center;
                }
            }
            .m-r-0 {
                margin-right: 0;
            }
        }
        .class-list2,
        .class-list3 {
            margin-top: 50px;
        }
    }
    
    .cont1,
    .cont2,
    .cont3 {
        position: relative;
        width: 100%;
        background-color: #f5f5f6;
        // margin-top: 30px;
        transition: height .6s;
        .cont-item {
            display: none;
            position: absolute;
            top: 0;
            width: 100%;
            // height: 200px;
            .classify-box {
                width: 100%;
                padding-top: 15px;
                padding-bottom: 30px;
                padding-left: 20px;
                padding-right: 20px;
                li {
                    float: left;
                    // padding: 5px 30px;
                    padding: 20px 52px 20px 32px;
                    // background-color: pink;
                    font-size: 14px;
                    // margin: 0 12px;
                    // margin-top: 20px;
                    color: #373d41;
                    cursor: pointer;
                    // transition: all .3s ease-in-out;
                    &:hover {
                        background: #fff url(../images/index/arrow-right.png) center right no-repeat;
                        box-shadow: 0 0 8px 0 rgba(0, 0, 0, .15);
                    }
                }
            }
        }
    }
    
    .cont2,
    .cont3 {
        height: 0px;
