    // 下载
    .download {
        height: 280px;
        padding-top: 220px;
        div {
            float: left;
            width: 600px;
            p {
                text-align: center;
                font-size: 44px;
                line-height: 100px;
                span {
                    color: #ee583f;
                    font-size: 60px;
                }
            }
            a {
                display: block;
                width: 302px;
                height: 62px;
                margin-bottom: 32px;
                margin-top: 15px;
                border-radius: 35px;
                overflow: hidden;
                margin-left: 175px;
                transition: all .3s;
            }
            .apple {
                background: url(../images/index/组66.png) -2px -2px no-repeat;
            }
            .android {
                background: url(../images/index/组65.png) -2px -2px no-repeat;
            }
            .apple:hover {
                background: url(../images/index/组63.png) -2px -2px no-repeat;
                background-color: #000;
            }
            .android:hover {
                background: url(../images/index/组64.png) -2px -2px no-repeat;
                background-color: #000;
            }
        }
    }